<template>
  <aside class="sidebar">
    <div class="sidebar__container">
      <section class="sidebar__section profile">
        <c-avatar class="profile__avatar" :src="getAccount.avatar" size="100" radius="50%" />
        <div class="profile__name">{{ getAccount.name }}</div>
        <div class="profile__stack">{{ getAccount.stack }}</div>
        <div class="profile__rate">{{ getAccount.price }}</div>
      </section>

      <section class="sidebar__section">
        <nav :class="[ 'menu', { 'menu--active': ~getActive } ]" :style="{ '--menu-index': getActive }">
          <router-link
            :class="[ 'menu__item', { 'menu__item--active': getActive === i } ]"
            :to="item.route"
            v-for="(item, i) in menu"
            :key="i"
          >
            <component class="menu__icon" :is="item.icon" v-if="item.icon" />
            <span class="menu__label">{{ item.label }}</span>
          </router-link>
        </nav>
      </section>
      
      <section class="sidebar__section">
        <c-tags class="sidebar__tags" :items="getTags" />
      </section>
    </div>
  </aside>
</template>
  
<script>
import { mapGetters } from 'vuex'

export default {
  name: 'l-sidebar',

  components: {
    cAvatar: () => import('@/components/Avatar'),
    cTags: () => import('@/components/Tags')
  },

  props: {
    menu: {
      type: Array,
      required: true
    }
  },

  computed: {
    ...mapGetters({
      getWorks: 'worklist/getWorks',
      getProjects: 'worklist/getProjects',
      getTags: 'worklist/getTags',
      getAccount: 'account/get'
    }),

    getActive () {
      return this.menu.findIndex(item => item.route.name === this.$route.name)
    }
  }
}
</script>

<style scoped>
  .sidebar {
    padding-right: 10px;
    box-sizing: border-box;
  }
  .sidebar__section + .sidebar__section {
    margin-top: 30px;
  }
  .sidebar__tags {
    font-size: 14px;
    justify-content: center;
  }
  .sidebar__container {
    position: sticky;
    top: 70px;
  }

  .profile {
    position: relative;
  }
  .profile__avatar {
    margin: 0 auto;
    box-shadow: 0 1px 1px rgba(0,0,0,.1),
                0 2px 2px rgba(0,0,0,.1),
                0 4px 4px rgba(0,0,0,.1);
  }
  .profile__name {
    margin: 10px 0 5px;
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    color: hsl(var(--ui-primary));
  }
  .profile__stack {
    text-align: center;
    font-size: 13px;
    margin-top: -4px;
    color: hsla(var(--ui-primary), .7);
  }
  .profile__rate {
    position: absolute;
    color: hsl(var(--ui-primary));
    top: 0;
    right: 5px;
    font-weight: 300;
  }

  .menu {
    display: flex;
    flex-direction: column;
    position: relative;
  }
  .menu__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px;
    text-decoration: none;
    font-size: 15px;
    position: relative;
    width: 100%;
    user-select: none;
    outline: none;
    box-sizing: border-box;
    color: hsl(var(--ui-primary-h), var(--ui-primary-s), 70%);
    transition: color .2s;
  }
  .menu__item:first-child::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    opacity: 0;
    border-left: 2px solid hsl(var(--ui-active));
    background: hsl(var(--ui-active-h), var(--ui-active-s), 98%);
    transition-property: border-left-color, background, opacity;
    transition-duration: .3s;
  }
  .menu--active .menu__item:first-child::after {
    opacity: 1;
    transform: translateY(calc(100% * var(--menu-index)));
    transition-property: transform, border-left-color, background, opacity;
  }
  .menu__item:hover,
  .menu__item--active {
    color: hsl(var(--ui-active));
  }
  .menu__icon {
    width: 18px;
    margin-right: 13px;
  }
  .menu__label {
    flex: 1;
  }
</style>